import {Observable} from "rxjs";
import * as CryptoJS from 'crypto-js';
export class Local {


   public static key = CryptoJS.enc.Base64.parse("HackersSeeIT2");
    public static  password = "HackersSeeIT2";
   public static  iv = CryptoJS.enc.Base64.parse("#base64IV#");
    constructor()
    {

    }

    public static setItem(name: string,message: Object):Observable<any>
    {


            return new Observable(observer => {
                var encrypted = CryptoJS.AES.encrypt(JSON.stringify(message), Local.password);
                localStorage.setItem(name, encrypted.toString());
                observer.next(message);
                observer.complete();
            })

    }

    public static getItem(name:string):Observable<any>
    {


        return new Observable(observer => {
                var decrypted = CryptoJS.AES.decrypt(localStorage.getItem(name), Local.password).toString(CryptoJS.enc.Utf8);
                observer.next(JSON.parse(decrypted));
                observer.complete();
            });



    }

    public static remove(name: string)
    {
       localStorage.removeItem(name);
    }

    public static removeAll()
    {
        localStorage.clear();
    }
}



