import {CommonModule} from "@angular/common";
import {BrowserModule} from "@angular/platform-browser";
import {RouterModule, Routes, Router} from "@angular/router";
import {NgModule} from "@angular/core";
import {AppComponent} from "./app.component";
import {LoginComponent} from "./login/login.component";
import {HomeComponent} from "./home/home.component";
import {CanActivateViaAuthGuard} from "./Guard";
import {DashboardComponent} from "./dashboard/dashboard.component";
import {ScheduleComponent} from "./schedule/schedule.component";
import { DispatchComponent } from "./dispatch/dispatch.component";
import {ScheduleEditComponent}  from "./schedule-edit/schedule-edit.component";

import { BookingComponent } from './booking/booking.component';
import {BaseComponent} from "./Core/BaseComponent";




const routes: Routes =[

    {path: 'login',component: LoginComponent },
    {path: '',pathMatch: "full", redirectTo:'login' },

    {path: 'home',component: HomeComponent,children:[

        {path: 'dashboard',component: DashboardComponent,canActivate:[CanActivateViaAuthGuard] },
        {path: 'dispatch',component: DispatchComponent,canActivate:[CanActivateViaAuthGuard] },
        {path: 'booking',component: BookingComponent,canActivate:[CanActivateViaAuthGuard]},
        {path: 'schedule',component: ScheduleComponent,canActivate:[CanActivateViaAuthGuard]},
        {path: 'schedule_edit/:id',component: ScheduleEditComponent,canActivate:[CanActivateViaAuthGuard]},
        {path: '',pathMatch: "full", redirectTo:'dashboard' },

        ]
    },
];



@NgModule({
    imports: [
        CommonModule,
        BrowserModule,
        RouterModule.forRoot(routes , { useHash: true })
    ],
    exports: [
    ],
})
export class AppRoutingModule extends BaseComponent {
    
    

    constructor(private route: Router){
      super();
        //console.log(this.helper.redirect_login_object);
        //this.helper.redirect_login_object= this.route;
        
    }
}


