import {Injectable} from "@angular/core";
import {getConst, http_error_handler, showNotification, Lang} from "../System/commonfunction";
import {HttpClient} from "@angular/common/http";
import {BaseComponent} from "../BaseComponent";
import {Router} from "@angular/router";



@Injectable()
export class Http
{
    constructor(private http:HttpClient,private router:Router)
    {


    }

    public commonResponseHandler(data,subscribe)
    {

        if(data.hasOwnProperty("success"))
        {
                if(data.hasOwnProperty("error_code") && data.error_code == 732)
                {

                    if(subscribe.object.hasOwnProperty("ngOnDestroy")){
                        subscribe.object.ngOnDestroy();
                    }
                    this.router.navigate(['login']);
                    
                    showNotification({from:"top",align:"center",type:"danger",message:Lang().please_login});
                    return false;
                }else if(data.hasOwnProperty("error_message") && data.error_code != 731){
                    showNotification({from:"top",align:"center",type:"danger",message:data.error_message});
                }
                subscribe.complete(subscribe.object,data);
                return true;

        }
        else {

            showNotification({from:"top",align:"center",type:"danger",message:Lang().something_went_wrong});
        }
    }

    public getData(url: string,param: any,subscribe:Subscribe)
    {
        return this.http.get(getConst().base_url+url).subscribe((data) => this.commonResponseHandler(data,subscribe),(error) => http_error_handler(subscribe,error,subscribe.error_priority));
    }


    public postData(url: string,param: any,subscribe:Subscribe)
    {

        return this.http.post(getConst().base_url+url,param).subscribe((data) =>(this.commonResponseHandler(data,subscribe)),(error) => http_error_handler(subscribe,error,subscribe.error_priority));
    }



}

interface Subscribe
{
    object:any;
    complete?: (object,data) => void;
    error_priority?:priority;
}

enum priority
{
    High = 5,
    Low = 1

}






