import {getLang} from "../Lang/LangLoader";
import {Local} from "../Storage/local";
import {Const as baseConst} from "../baseConst";
import {FormGroup} from "@angular/forms";
import {ErrorComponent} from "../../error-component/error-component.component";
import {Router} from "@angular/router/router";
import set = Reflect.set;

export var system_settings : any={};
export var redirect_login_object : any = {};



declare var $:any;


export function Lang()
{

    return getLang(system_settings['language']?system_settings['language']:"en");
}

export function setConfig(data)
{


   Local.setItem("vhsdghsdghsdgh",data).subscribe((data) => system_settings=data,(error) => {console.log("seterror");console.log(error);},() => console.log('file set'));
   // (new Local()).setItem("test",{language:true}).subscribe((data) => console.log(data),(error) => console.log(error),() => console.log('test'));


}

export function getConfig()
{

    Local.getItem("vhsdghsdghsdgh").subscribe((data) => {system_settings=data;console.log("getdata");},(error) =>{console.log("file");console.log(this.redirect_login_function());},() => console.log('file get'));

}
export function redirect_login_function(){


//check  router  has navigate key
    let myProp : string = "navigated";

    if(this.redirect_login_object.hasOwnProperty(myProp)){
        this.http_error_handler(null,null,6);
        this.redirect_login_object.navigate(['/login']);
    }

}

export function clearLocaldata()
{
    Local.remove("vhsdghsdghsdgh");
}

export function getConst()
{
    return baseConst;
}
export function stop_timer(time)
{
    for(let i=0 ; i<time.length; i++){
        clearInterval(time[i]);
    }

}



export function DynamicLoader(object,component:component_loader)
{

    let componentFactory = object.resolver.resolveComponentFactory(component.component);
    let viewContainerRef = object.errorhost.viewContainerRef;
    let view =  viewContainerRef.createComponent(componentFactory);
    view.instance[component.param]=component.data;
    return view;

}



export function setValidatorMessage(form:FormGroup,error:errorMessage[])
{

    error.forEach(function (value,index) {

        form['controls'][value['formName']]['statusChanges'].subscribe(
            (data) => {
                if(form['controls'][value['formName']]['errors'] != null)
                {
                    for(var prop in form['controls'][value['formName']]['errors'])
                    {
                        form.controls[value['formName']]['error_message']=value['message'][prop]?value['message'][prop]:"";
                       break;
                    }

                }
            }
        );
    })


}


export function http_error_handler(subscribe,error,priority=1)
{
    switch(priority)
    {
        case 1:
            showNotification({from:"top",align:"center",type:"danger",message:Lang().http_error_message});
            break;
        case 5:
            DynamicLoader(subscribe.object,{component:ErrorComponent,data:Lang().http_error_message,param:"post"});
            break;
        case 6:
            /*Session expired please login*/
            showNotification({from:"top",align:"center",type:"danger",message:Lang().please_login});
            break;
        case  7:
            /*No driver found*/
            showNotification({from:"top",align:"center",type:"danger",message:Lang().no_service});
            break;
        case  8:
            /*No driver found*/
            showNotification({from:"top",align:"center",type:"danger",message:Lang().no_service});
            break;

        default:
            showNotification({from:"top",align:"center",type:"danger",message:Lang().http_error_message});
            break;

    }
}

export function showNotification(data:Notify)
{


    $.notify({
        icon: "notifications",
        message: data.message

    },{
        type: data.type,
        timer: 2000,
        placement: {
            from: data.from,
            align: data.align
        }
    });
}


interface component_loader
{
    component:any;
    data:any;
    param:string;
}

interface errorMessage
{
    formName:string;
    message:[any]|{};
}

interface Notify {
    from:"top"|"bottom"|"left"|"right";
    align: "top"|"bottom"|"left"|"right"|"center";
    type?: "success"|"info"|"warning"|"danger";
    message: string;
}




