import {Component, OnInit, Input, ViewChild, ElementRef} from '@angular/core';

@Component({
  selector: 'app-eta',
  templateUrl: './eta.component.html',
  styleUrls: ['./eta.component.css']
})
export class EtaComponent implements OnInit {

  @Input() data:any;
  @Input() object:any;
  @ViewChild('eta1') eta1:ElementRef;
  @ViewChild('eta2') eta2:ElementRef;

  constructor() {

  }

  public toggleside(area)
  {

    if(area == 'side1')
    {
      this.eta1.nativeElement.style.display="none";
      this.eta2.nativeElement.style.display="block";
    }
    else
    {
      this.eta1.nativeElement.style.display="block";
      this.eta2.nativeElement.style.display="none";
    }
  }

  ngOnInit() {



  }

}



