import {Injectable} from "@angular/core";
import {CanActivate, Router} from "@angular/router";
import {getConfig, system_settings, clearLocaldata} from "./Core/System/commonfunction";
//import isJSDocReturnTag = ts.isJSDocReturnTag;



@Injectable()
export class CanActivateViaAuthGuard implements CanActivate {

    constructor(private route:Router) {}

    canActivate() {


            getConfig();
            if (system_settings == null || (!system_settings.hasOwnProperty("token") && !system_settings.hasOwnProperty("id"))) {

                this.route.navigate(['/login']);
                return false;
            }
        return true;
    }


}


