import {Component, OnInit, ViewChild, ComponentFactoryResolver, AfterViewInit} from '@angular/core';
import {BaseComponent} from "../Core/BaseComponent";
import {Http} from "../Core/Http/http";
import * as Chartist from 'chartist';
import {ErrorDirective} from "../Core/error.directive";
import {ErrorComponent} from "../error-component/error-component.component";
import {LoginComponent} from "../login/login.component";
import {DyanmicComponent} from "../dyanmic/dyanmic.component";
import {Router} from "@angular/router";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent extends BaseComponent implements OnInit,AfterViewInit {

  public completed_trips;
  public cancelled_trips;
  public ongoing_trips;
  public dashData;



  constructor(private resolver:ComponentFactoryResolver,private http:Http) {
 super();


  }

  ngAfterViewInit()
  {

  }


  startAnimationForLineChart(chart){
    let seq: any, delays: any, durations: any;
    seq = 0;
    delays = 80;
    durations = 500;

    chart.on('draw', function(data) {
      if(data.type === 'line' || data.type === 'area') {
        data.element.animate({
          d: {
            begin: 600,
            dur: 700,
            from: data.path.clone().scale(1, 0).translate(0, data.chartRect.height()).stringify(),
            to: data.path.clone().stringify(),
            easing: Chartist.Svg.Easing.easeOutQuint
          }
        });
      } else if(data.type === 'point') {
        seq++;
        data.element.animate({
          opacity: {
            begin: seq * delays,
            dur: durations,
            from: 0,
            to: 1,
            easing: 'ease'
          }
        });
      }
    });

    seq = 0;
  };

  startAnimationForBarChart(chart){
    let seq2: any, delays2: any, durations2: any;

    seq2 = 0;
    delays2 = 80;
    durations2 = 500;
    chart.on('draw', function(data) {
      if(data.type === 'bar'){
        seq2++;
        data.element.animate({
          opacity: {
            begin: seq2 * delays2,
            dur: durations2,
            from: 0,
            to: 1,
            easing: 'ease'
          }
        });
      }
    });

    seq2 = 0;
  };




  renderDashboard(object,data)
  {
    console.log("data");
    console.log(data);
    object.dashData=data;
      object.completed_trips = data.dispatch_dashboard.completedTrips;
      object.cancelled_trips = data.dispatch_dashboard.cancelledTrips;
      object.ongoing_trips = data.dispatch_dashboard.ongoingTrips;
    object.drawchart();
  }

  drawchart()
  {
    this.green_line_chart();
    this.bar_chat();
    this.red_chart();
  }

  bar_chat(){
    var date1=[];
    var sum=[];

    for (var prop in this.dashData.dispatch_dashboard.last12MonthsCompletedTrips) {
      date1.push(prop);
      sum.push(this.dashData.dispatch_dashboard.last12MonthsCompletedTrips[prop]);
    }





    /* ----------==========     Completed Tasks Chart initialization    ==========---------- */

    const dataCompletedTasksChart: any = {
      labels: date1,
      series: [
        sum
      ]
    };

    const optionsCompletedTasksChart: any = {
      lineSmooth: Chartist.Interpolation.cardinal({
        tension: 0
      }),
      low: 0,
      high: 400, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
      chartPadding: { top: 0, right: 0, bottom: 0, left: 0}
    }

    var completedTasksChart = new Chartist.Line('#completedTasksChart', dataCompletedTasksChart, optionsCompletedTasksChart);

    // start animation for the Completed Tasks Chart - Line Chart
    this.startAnimationForLineChart(completedTasksChart);
  }


  /*
   * draw red chart
   * */

  red_chart(){
    var date1=[];
    var sum=[];


    for (var prop in this.dashData.dispatch_dashboard.last12MonthsCancelledTrips) {
      date1.push(prop);
      sum.push(this.dashData.dispatch_dashboard.last12MonthsCancelledTrips[prop]);
    }




    /* ----------==========     Emails Subscription Chart initialization    ==========---------- */

    var dataEmailsSubscriptionChart = {
      labels: date1,
      series: [
        sum

      ]
    };
    var optionsEmailsSubscriptionChart = {
      axisX: {
        showGrid: false
      },
      low: 0,
      high: 1000,
      chartPadding: { top: 0, right: 5, bottom: 0, left: 0}
    };
    var responsiveOptions: any[] = [
      ['screen and (max-width: 640px)', {
        seriesBarDistance: 5,
        axisX: {
          labelInterpolationFnc: function (value) {
            return value[0];
          }
        }
      }]
    ];
    var emailsSubscriptionChart = new Chartist.Bar('#emailsSubscriptionChart', dataEmailsSubscriptionChart, optionsEmailsSubscriptionChart, responsiveOptions);

    //start animation for the Emails Subscription Chart
    this.startAnimationForBarChart(emailsSubscriptionChart);
  }



  green_line_chart(){
    var date1=[];
    var sum= [];

    for (var prop in this.dashData.dispatch_dashboard.last7DaysCompletedTrips) {
      date1.push(prop);
      sum.push(this.dashData.dispatch_dashboard.last7DaysCompletedTrips[prop]);
    }

    /*  */

    const dataDailySalesChart: any = {

      labels: date1,
      series: [
        sum
      ]
    };

    const optionsDailySalesChart: any = {
      lineSmooth: Chartist.Interpolation.cardinal({
        tension: 0
      }),
      low: 0,
      high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
      chartPadding: { top: 0, right: 0, bottom: 0, left: 0},
    }

    var dailySalesChart = new Chartist.Line('#dailySalesChart', dataDailySalesChart, optionsDailySalesChart);

    this.startAnimationForLineChart(dailySalesChart);
  }





  ngOnInit() {

    this.http.postData(this.helper.getConst().dashboard,{id:this.helper.system_settings.id,token:this.helper.system_settings.token},{object:this,error_priority:5,complete: this.renderDashboard});

  }

}



