
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {Http} from "./Core/Http/http";
import {HttpClientModule} from "@angular/common/http";
import {AppRoutingModule} from "./app.routing";
import {RouterModule} from "@angular/router";
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import {ComponentsModule} from "./Component/components.module";
import {CanActivateViaAuthGuard} from "./Guard";
import { DashboardComponent } from './dashboard/dashboard.component';
import { DispatchComponent } from './dispatch/dispatch.component';
import {ErrorComponent} from './error-component/error-component.component';
import { DyanmicComponent } from './dyanmic/dyanmic.component';
import {ErrorDirective} from "./Core/error.directive";
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';



/*Eta shows*/
import { EtaComponent } from './eta/eta.component';
import {EtaDirective} from "./Directive/EtaDirective";


/*Trip handle*/
import { TriphandlerComponent } from './triphandler/triphandler.component';
import {TriphandleDirective} from "./Directive/TriphandleDirective";


/*Trip later*/
import { TriplaterComponent } from './triplater/triplater.component';
import { TriplaterDirective } from "./Directive/TriplaterDirective";
//import {DpDatePickerModule} from 'ng2-date-picker';
import { AngularDateTimePickerModule } from 'angular2-datetimepicker';
import { BookingComponent } from './booking/booking.component';
import { RedirectComponent } from './redirect/redirect.component';
import { ScheduleComponent } from './schedule/schedule.component';
import { ScheduleEditComponent } from './schedule-edit/schedule-edit.component';
import { TimepickerModule } from 'ngx-bootstrap/timepicker';






@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HomeComponent,
    DashboardComponent,
    DispatchComponent,
    ErrorComponent,
    DyanmicComponent,
    ErrorDirective,
      
    EtaComponent,
    EtaDirective,
      
    TriphandleDirective,
    TriphandlerComponent,
      
    TriplaterComponent,
    TriplaterDirective,
    BookingComponent,
    RedirectComponent,
    ScheduleComponent,
    ScheduleEditComponent,

  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    HttpClientModule,
    AppRoutingModule,
    RouterModule,
    ComponentsModule,
    AngularDateTimePickerModule,
    BsDatepickerModule.forRoot(),
    TimepickerModule.forRoot(),
  ],
  providers: [Http,CanActivateViaAuthGuard],
  bootstrap: [AppComponent],
  entryComponents: [ErrorComponent,EtaComponent,TriphandlerComponent,TriplaterComponent]
})
export class AppModule { }



